<template>
       <div class="content">
      <ul class="content-ul">
        <div v-show="!tasks.length" class="no-task">没有任务哦</div>
       <task v-for="task in tasks" 
          :per="task.per"
          :size="task.size"
          :filename="task.filename"
          :filepath="task.filepath"
          :begintime="task.begintime"
          :processtime="task.processtime"
          :id="task.id"
        :key="task.id" @delete="ondelete"></task>
      </ul>
      <button @click="createTask" class="content-button">create</button>
    </div>
</template>
<style  scoped>
.no-task{
    width: 100%;
    height: 80%;
    position: absolute;
    font-size: 40px;
    opacity: 0.8;
    text-align: center;
    left: 50%;
    top:40%;
    margin-left:-50%;
}
.content{
  width: 100%;
  overflow: hidden;
  position: relative;
  height: calc(100vh - 200px);
  
  box-shadow: 0px 0px 10px rgba(255,255,255,0.5);
}
.content-ul{
  width: 100%;
  margin-top:10px;
  height: 85%;
  overflow: scroll;
}
.content-li{
  width: 100%;
  height: 36px;
  list-style: none;
  margin-bottom: 4px;

}
.content-li-block{
  height: 100%;
  background-color: rgba(223,244,236);
  float: left;
  padding: 10px;
  margin-right: 2px;
  margin-left: 2px;
  box-shadow: 0px 0px 10px rgba(223,244,236);
  cursor: pointer;
  max-height: 1em;
  line-height: 1em;
  font-weight: 600;
}
.content-button{
  width: 200px;
  height: 60px;
  position: absolute;
  bottom: 10px;
  background-color: rgba(58,165,109);
  outline: none;
  color: #fff;
  font-size: 1.5em;
  left:50%;
  margin-left: -100px;
  border-radius: 30px;
  box-shadow: 0px 2px 5px rgba(58,165,109);
  border:none;
  cursor: pointer;
}
.content-button:hover{
  box-shadow: 0px 2px 10px 2px rgba(58,165,109);
  font-size: 1.7em;
}
.longger{
  width: calc(100% - 240px);
}
.right{
  float: right;
}
.icon{
  margin-right:5px;
}
</style>
<script>
import task from '@/components/task'
let status = {
          per:100,
          size:"100mb",
          filename:"高等数学资料",
          filepath:"/chengbo/img/",
          begintime:"2020/02/04",
          processtime:368,
          id:0,
      }
export default {
  name: 'index',
  components:{task},
  data () {
    return {
      title:'downtool server',
      taskNum:0,
      tasks:[],
    }
  },
  methods:{
      createTask(){
          let curStatus = Object.assign({},status);
          this.$swal({
            html:
    '<input id="swal-input1" placeholder="请输入URL地址" class="swal2-input">' +
    '<input id="swal-input2" placeholder="请输入文件名" class="swal2-input">',
  focusConfirm: false,
  preConfirm: () => {
    return [
      curStatus.url = document.getElementById('swal-input1').value,
      curStatus.filename = document.getElementById('swal-input2').value
    ]
  }
            }).then((res)=>{
                console.log(res);
            this.$swal({
        title: '请选择路径',
        input: 'select',
        inputOptions: {
            book: '书籍',
            video: '电影',
            photo: '图片',
            other: '其他'
        },
        inputPlaceholder: '选择合适的路径',
        showCancelButton: true,
  
        }).then((res)=>{
            if (res.value === 'book') {
          curStatus.filepath="book/";
      } else if(res.value == 'video'){
          curStatus.filepath="video/"
      }else if(res.value === 'image'){
          curStatus.filepath="image/"
      }else if(res.value === 'other'){
          curStatus.filepath="other/"
        }else{
            curStatus.filepath="other/"
      }
            curStatus.id = this.tasks.length;
            console.log(curStatus);
        }).then((res)=>{
            this.$axios.post('/createTask',{
            task: JSON.stringify(curStatus),
        }).then(function(res){
                    console.log(res)
                },function(){
                    console.log('createTask失败');
                });
})
            })
      },
      ondelete(id){
          let task = null;
          for(let i=0;i<this.tasks.length;i++){
              if(this.tasks.id==id)
                tasks = this.tasks[i];
                break;
          }
          this.tasks.splice(this.tasks.indexOf(task),1);
      },
      getTask(){
          var that = this;
          setInterval(()=>{
              this.$axios.get('/getTask').then(function(res){
                    console.log(res)
                    for (let i in res.data){
                        console.log(i)
                        that.tasks = res.data
                    }   
                },function(){
                    console.log('请求失败处理');
                });
          },2000);
           
      },
      
  },
  mounted(){
      this.getTask();
  }
}
</script>


